/*=======================================================

	ベース

========================================================*/
html {
  font-size: 62.5%;
}

body {
  font-family: "Hiragino Sans", "Hiragino Kaku Gothic Pro", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  letter-spacing: 0.065em;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  backface-visibility: hidden;
  height: auto;
  image-rendering: -webkit-optimize-contrast;
  max-width: 100%;
  vertical-align: bottom;
}

a {
  display: inline-block;
  backface-visibility: hidden;
  color: inherit;
  text-decoration: none;
  transition: opacity 0.5s;
}
a:hover {
  opacity: 0.7;
  cursor: pointer;
}
a:focus {
  outline: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* =======================================================
	function：数字から単位を取り除く

	@function f-strip-unit
	----------------------------------------
	$number：単位を取り除きたい値（ex. 1920px）
	@return：単位が取り除かれた数値（ex. 1920）
======================================================= */
/* =======================================================
	function：px を vw に変換

	@function f-vw-pc / f-vw-sp
	----------------------------------------
	$px：vwに変換するpxの値（ex. 60）
	$base-pc：基準とするPCデザインカンプのサイズ（ex. 1920）
	$base-sp：基準とするSPデザインカンプのサイズ（ex. 375）
	@return：vwに変換された値（ex. 1920）
======================================================= */
/* =======================================================
	mixin：font-sizeをvwで一括指定する（依存：func-strip-unit）

	@include m-fontsize-vw
	----------------------------------------
	$base：設定したいフォントサイズ（ex. 1920px）
	$max：vwを解除するwindow幅上限（ex. 1920px）
	$min：vwを解除するwindow幅下限（ex. 767px）
	$pc：pcのフォントサイズ（ex. 16px）
	$sp：spのフォントサイズ（ex. 12px）
======================================================= */
/* =======================================================
	mixin：font-sizeをremで一括指定する（依存：func-strip-unit）

	@include m-fontsize-rem
	----------------------------------------
	$min-width：media queryで設定したいmin-width（ex. 768px）
	$pc：pcのフォントサイズ（ex. 16px）
	$sp：spのフォントサイズ（ex. 12px）
======================================================= */
/* =======================================================
	mixin：media queryの管理

	@include mq-up / mq-down
	----------------------------------------
	$breakpoint-up：min-width ×××× px以上の場合
	$breakpoint-down：max-width ×××× px未満の場合
	$breakpoint：breakpointの指定（ex. md）
======================================================= */
/*=======================================================

	utility

========================================================*/
.u-inner {
  width: 100%;
  margin: auto;
  max-width: 1400px;
  padding: 0 30px;
}
@media screen and (max-width: 767px) {
  .u-inner {
    padding: 0 20px;
  }
}

@media screen and (max-width: 767px) {
  .u-pcOnly {
    display: none !important;
  }
}

@media screen and (min-width: 768px) {
  .u-spOnly {
    display: none !important;
  }
}

/*=======================================================
	アニメーション：フェードイン 

	1. フェードインさせたい要素に「u-fade-in / u-fade-up」を付与する
	2. 表示するタイミングに、js側で「is-show」を付与する
========================================================*/
.u-fade-in {
  opacity: 0;
  transition: opacity 1s;
}
.u-fade-in.is-show {
  opacity: 1;
}

.u-fade-up {
  opacity: 0;
  transform: translateY(40px);
  transition: opacity 1s, transform 1s;
}
.u-fade-up.is-show {
  opacity: 1;
  transform: translateY(0);
}

/*=======================================================

	header
	media query : header

========================================================*/
.l-header {
  width: 100%;
  height: 105px;
  background-color: whitesmoke;
  position: relative;
  z-index: 10;
}
.l-header.isOpen .l-header__menu {
  right: 0;
}

.l-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 10px 5%;
  position: fixed;
  top: 0;
  left: 0;
}

.l-header__menu {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0;
  right: -100%;
  transition: right 0.5s;
  z-index: 10;
}
@media screen and (min-width: 768px) {
  .l-header__menu {
    width: auto;
    height: auto;
    background-color: transparent;
    text-align: right;
    position: static;
  }
}

.l-header__menuItem {
  display: inline-block;
}
@media screen and (min-width: 768px) {
  .l-header__menuItem {
    margin-left: 30px;
  }
}

.c-nav-trigger {
  z-index: 100;
}

/*=======================================================

	footer

========================================================*/
.l-footer {
  background-color: #c46565;
  margin-top: auto;
  padding: 0.9375vw 0 0.8333333333vw;
}
.l-footer small {
  display: block;
  color: #000;
  text-align: center;
  font-size: 0.8333333333vw;
}
@media screen and (min-width: 1920) {
  .l-footer small {
    font-size: 1.6rem;
  }
}
@media screen and (max-width: 768px) {
  .l-footer small {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 767px) {
  .l-footer small {
    font-weight: 300;
    padding: 15px 10px;
  }
}

/*========================================================

	nav trigger
	media query : header

========================================================*/
.c-nav-trigger {
  display: inline-block;
  transition: all 0.5s;
  box-sizing: border-box;
  position: relative;
  width: 30px;
  height: 26px;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .c-nav-trigger {
    display: none;
  }
}
.c-nav-trigger span:nth-of-type(1),
.c-nav-trigger span:nth-of-type(2),
.c-nav-trigger span:nth-of-type(3) {
  display: inline-block;
  transition: all 0.5s;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: gray;
  border-radius: 4px;
}
@media screen and (min-width: 768px) {
  .c-nav-trigger span:nth-of-type(1),
.c-nav-trigger span:nth-of-type(2),
.c-nav-trigger span:nth-of-type(3) {
    height: 4px;
  }
}
.c-nav-trigger span:nth-of-type(1) {
  top: 0;
}
.c-nav-trigger span:nth-of-type(2) {
  top: 12px;
}
@media screen and (min-width: 768px) {
  .c-nav-trigger span:nth-of-type(2) {
    top: 20px;
  }
}
.c-nav-trigger span:nth-of-type(3) {
  bottom: 0;
}

.isOpen.c-nav-trigger span:nth-of-type(1) {
  transform: translateY(12px) rotate(-45deg);
}
.isOpen.c-nav-trigger span:nth-of-type(2) {
  opacity: 0;
}
.isOpen.c-nav-trigger span:nth-of-type(3) {
  transform: translateY(-12px) rotate(45deg);
}

